import './ExcelViewer.scss';
import { Dispatch, SetStateAction, useEffect, useState } from 'react';
import { excelFileMapping } from './excelFileMapping';
import * as XLSX from 'xlsx';
import { ExcelViewerFileConfig, ExcelViewerTable } from './ExcelViewerModels';

function ExcelViewer (props: {permitType: string}) {

    const [data, setData]: [ExcelViewerTable, Dispatch<SetStateAction<ExcelViewerTable>>] = useState<ExcelViewerTable>({
        tableName: '',
        columns: [],
        rows: []
    });

    useEffect(() => {

        fetch(`./${excelFileMapping[props.permitType as keyof ExcelViewerFileConfig]}.xlsx`).then(res => res.blob()).then(res => {
            const f = res;
            // var name = f.name;
            const reader = new FileReader();
            reader.onload = (evt) => { // evt = on_file_select event
                /* Parse data */
                const bstr = evt?.target?.result;
                const wb = XLSX.read(bstr, {type:'binary'});
                /* Get first worksheet */
                const wsname = wb.SheetNames[0];
                const ws = wb.Sheets[wsname];
                console.log('Sheets', wb, ws);
                /* Convert array of arrays */
                const data = XLSX.utils.sheet_to_json(ws, {header: 1} as XLSX.Sheet2JSONOpts);
                /* Update state */
                if (data.length >= 2) {
                    const [cols, ...rows]: [string[], string[]] = data as [string[], string[]];
                    setData({
                        tableName: wsname,
                        columns: cols,
                        rows: rows
                    })
                    console.log("Data \n", cols, rows);
                }
                
            }
            reader.readAsBinaryString(f);
        });
    }, [props.permitType])

    return data.columns.length && data.rows.length ?
    <div className="excel-viewer-container">
        <table>
            <thead>
                <tr>
                    {
                        data.columns.map((col: string, iHeader: number) => (
                            <th key={`${data.tableName}-header-${iHeader}`}>{col}</th>
                        ))
                    }
                </tr>
            </thead>
            <tbody>
            {
                data.rows.map((row: string[], iRow: number) => (
                    <tr key={`${data.tableName}-row-${iRow}`}>
                        {
                            [...Array(data.columns.length)].map((cell: string, iCell: number) => (
                                <td key={`${data.tableName}-cell-${iCell}`}>
                                    {
                                        row[iCell] ? row[iCell] : null
                                    }
                                </td>
                            ))
                        }
                    </tr>
                ))
            }
            </tbody>
        </table>
    </div>
    : <div>No Table Data Available</div>
}

export default ExcelViewer;